import { getToken } from '@/utils/auth';
import { upUserInfo } from '@/api/user.js';
export default {
	data() {
		return {
			from: ''
		};
	},
	methods: {
		appLoginWx() {
			let _that = this;
			uni.getProvider({
				service: 'oauth',
				success: function(res) {
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: res => {
								uni.getUserInfo({
									provider: 'weixin',
									success: info => {
										//这里请求接口
										console.log( info )
										let code = res.code;
										_that.$store
											.dispatch('codeToToken', {
												url: '/app/login',
												code: code
											})
											.then(() => {
												upUserInfo( info.userInfo )
													.then(() => {
														uni.showToast({
															title: '登录成功，返回中...',
															icon: 'none',
															duration: 1000,
															success: function() {
																setTimeout(function() {
																	uni.navigateTo({
																		url: `/pages/${_that.from}/index`
																	});
																}, 1000);
															}
														});
													})
													.catch(() => {
														uni.showToast({ title: '获取数据失败，请稍后再试', icon: 'none' });
													});
											});
									},
									fail: () => {
										uni.showToast({ title: '微信登录授权失败', icon: 'none' });
									}
								});
							},
							fail: () => {
								uni.showToast({ title: '微信登录授权失败', icon: 'none' });
							}
						});
					} else {
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon: 'none'
						});
					}
				}
			});
		}
	},
	onLoad: function(option) {
		this.from = option.from;
	}
};
